.games {
    background-color: #d8c6ae;
    width: 100%;
    min-height: 47em;
    min-width: 80em;
    height: 105vh;
    font-family: 'Roboto', sans-serif;
}

.gameComponents {
    height: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10em;
    position: relative;
}

.titleGames {
    width: 100%;
    text-align: left;
    color: #3c4046;
    padding-bottom: 3vh;
    font-family: 'Open Sans', sans-serif;
}

.titleAlignGames {
    min-width: 92%;
    width: 80%;
    display: flex;
    flex-direction: column;
}

.titleGames span {
    letter-spacing: 1px;
    font-size: 35px;
}

.gameWrapper {
    align-items: center;
    display: flex;
}

.gameCenter {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tile:hover {
    transform: scale(1.05);
}

.tile {
    transition: transform .2s ease;
}

.tileInner {
    height: 17em;
    width: 325px;
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #edeef1;
    cursor: pointer;
}

.tileTitle {
    font-weight: bold;
    font-size: 17px;
    margin-top: 1px;
    color: #6a7079;
}

.tileDescription {
    font-size: 13px;
    margin: 10px;
    bottom: 0;
    color: #3c4046;
}

.tileImage {
    height: 73%;
    width: 100%;
    background-size: cover;
    background-position: center;
    margin: 13px;
    margin-top: 0;
}

.popup-enter {
    opacity: 0;
    transform: scale(0.9);
}

.popup-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
}
.popup-exit {
    opacity: 1;
}
.popup-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
}

.gridContainer {
    width: 100%;
    top: 25vh;
    height: 75vh;
}

.gridWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.gridContent {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.gridTile {
    padding-top: 2vh;
    display: flex;
    flex-direction: row;
}

.overlayComponent {
    margin-left: -11%;
    margin-top: 50px;
    background-color: none;
    outline: none;
    position: absolute;
    justify-content: center;
    display: flex;
    position: center;
    width: 1350px;
    height: 90vh;
    background-color: #edeef1;
}

.exitButton {
    z-index: 1;
    outline: none;
    box-shadow: none;
    cursor: pointer;
    position: absolute;
    top: 1%;
    left: 96%;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: #3c4046;
}