.navBar {
  position: fixed;
  z-index : 2;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 75px;
  background-color: #fff;
  color: #333;
  transform: translateY(05);
  transition: transform 0.3s ease;
  font-family: 'Open Sans', sans-serif;
}

.navBar.shadow {
  box-shadow: 0 9px 14px -9px rgba(0, 0, 0, 0.13);
}

.navBar.hidden {
  transform: translateY(-110%);
}

.navBar ul {
  display: flex;
  list-style: none;
  justify-content: center;
  width: 100%;
  left: 50%;
  position: center;
  text-align: center;
}

.navBar ul a {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #3c4046;
  display: block;
  padding: 15px;
  margin: 0 5px;
  position: center;
}

.navBar ul a:hover {
  color: #ccaa7e;
  cursor: pointer;
}

.navBar ul .active {
  color: #dda04f;
}
